/* App.css */
body {
  font-family: 'Helvetica Neue', sans-serif;
  background: linear-gradient(135deg, #a7c3f1, #f0f4ff);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  padding: 20px;
}

.container {
  text-align: center;
  background: white;
  padding: 2.5em;
  border-radius: 15px;
  width: 350px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
  transition: transform 0.2s ease;
}

.container:hover {
  transform: translateY(-5px);
}

h1 {
  font-size: 2.5em;
  color: #334155;
  margin-bottom: 0.5em;
  font-weight: 600;
  letter-spacing: 1px;
}

button {
  padding: 0.6em 1.4em;
  font-size: 1.1em;
  color: #ffffff;
  background: linear-gradient(90deg, #3b82f6, #1d4ed8);
  border: none;
  border-radius: 25px;
  cursor: pointer;
  transition: background 0.3s ease, transform 0.2s ease;
  margin-right: 10px;
}

button:hover {
  background: linear-gradient(90deg, #1d4ed8, #3b82f6);
  transform: translateY(-2px);
}

button:focus {
  outline: none;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.5);
}

button:disabled {
  background: #a1c3f1;
  cursor: not-allowed;
}
